      display: none;
      position: fixed;
      padding: 70px 30px 0 30px;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      background: linear-gradient(94.26deg, #60467e 9.51%, #6d5aeb 90.23%);
      z-index: 9; }
      .header__menu-container_visible {
        display: block; } }
  @media (max-width: 992px) {
    .header__nav {
