      opacity: 0;
      visibility: hidden;
      transition: 0.3s;
      background: #730aec;
      transform: translate(0, -20px); }
      @media (max-width: 992px) {
        .header__menu .sub-menu {
          position: static;
          opacity: 1;
          visibility: visible;
          transform: translate(0, 0);
          background: transparent;
          padding-left: 30px; } }
      .header__menu .sub-menu > .menu-item {
        width: 100%; }
        .header__menu .sub-menu > .menu-item:hover > a {
          background: #371b57; }
          @media (max-width: 992px) {
            .header__menu .sub-menu > .menu-item:hover > a {
              background: transparent; } }
        .header__menu .sub-menu > .menu-item:hover .sub-menu {
          display: block;
          position: absolute;
          left: 100%;
          top: 0; }
          @media (max-width: 992px) {
            .header__menu .sub-menu > .menu-item:hover .sub-menu {
              display: block;
              position: static;
              padding-left: 20px;
              opacity: 1;
              visibility: visible;
              transform: translate(0, 0); } }
        @media (max-width: 992px) {
          .header__menu .sub-menu > .menu-item > a {
            font-size: 16px; } }
      .header__menu .sub-menu .sub-menu {
        display: none;
        position: absolute;
        top: 0;
        left: 0; }
        @media (max-width: 992px) {
          .header__menu .sub-menu .sub-menu {
            display: block;
            position: static;
            padding-left: 20px;
            opacity: 1;
            visibility: visible;
            transform: translate(0, 0); } }
        .header__menu .sub-menu .sub-menu .menu-item > a {
